<template>
  <v-container
    id="calendar"
    fluid
    tag="section"
  >
    <v-row>
      <v-col
        cols="2"
        sm="3"
        md="2"
      >
        <v-btn
          class="ma-2"
          outlined
          fab
          x-small
          color="primary"
          @click="previousMonth"
        >
          <v-icon>mdi-arrow-left-bold</v-icon>
        </v-btn>
        <v-btn
          class="ma-2"
          outlined
          fab
          x-small
          color="primary"
          @click="nextMonth"
        >
          <v-icon>mdi-arrow-right-bold</v-icon>
        </v-btn>
      </v-col>
      <v-col
        cols="3"
        sm="3"
        md="3"
      >
        <v-menu
          ref="dialog"
          v-model="dialog"
          :close-on-content-click="false"
          :return-value.sync="today"
          transition="scale-transition"
          offset-y
          min-width="290px"
          max-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="today"
              label="Текущая дата:"
              readonly
              v-bind="attrs"
              prepend-icon="mdi-calendar-blank-multiple"
              v-on="on"
            />
          </template>
          <v-date-picker
            v-model="today"
            no-title
            scrollable
          >
            <v-spacer />
            <v-btn
              text
              color="primary"
              @click="dialog = false"
            >
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="scrollToNew() && $refs.dialog.save(today)"
            >
              OK
            </v-btn>
          </v-date-picker>
        </v-menu>
      </v-col>
      <v-col
        cols="3"
        sm="3"
        md="3"
      >
        <v-select
          v-model="period"
          :items="periods"
          label="Период"
          @change="scrollToNew"
        />
      </v-col>
    </v-row>
    <v-col
      cols="12"
      md="4"
    >
      <v-text-field
        v-model="search"
        v-on:change="find"
        label="Номер брони у источника:"
        required
      />
    </v-col>
    <div
      ref="gstc"
      class="gstc"
    />
    <v-dialog
      v-model="$store.state.bookingModal"
      hide-overlay
      persistent
      max-width="800px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-card-text>
          <question-reserve />
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="$store.state.reserve"
      hide-overlay
      persistent
      max-width="1200px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-card-text>
          <user-profile />
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="$store.state.toBook"
      hide-overlay
      persistent
      max-width="1200px"
      transition="dialog-bottom-transition"
    >
      <v-card>
        <v-card-text>
          <to-book />
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  import UserProfile from './pages/UserProfile'
  import ToBook from './components/ToBook'
  import GSTC from 'gantt-schedule-timeline-calendar'
  import 'gantt-schedule-timeline-calendar/dist/style.css'
  // eslint-disable-next-line no-unused-vars
  import store from '../../store'
  import { mapMutations, mapActions } from 'vuex'
  import QuestionReserve from './components/QuestionReserve'

  let state

  export default {
    name: 'App',
    components: {
      QuestionReserve,
      UserProfile,
      ToBook,
    },
    data () {
      return {
        period: 15,
        periods: [{ text: '7', value: 7 }, { text: '15', value: 15 }, { text: '30', value: 30 }, { text: '90', value: 90 }],
        dayInZoom: [{ text: '1:1', value: 21 }, { text: '1:2', value: 22 }],
        zoomSize: 20,
        gstc: null,
        dialog: false,
        today: new Date().toISOString().substr(0, 10),
        openModal: false,
        search: '',
      }
    },

    computed: {
      config: function () {
        return this.$store.state.config
      },
    },
    mounted () {
      state = GSTC.api.stateFromConfig(this.config)
      this.$emit('state', state)
      this.setRefs(this.$refs.gstc)
      window.GSTCInstance = this.gstc = GSTC({
        element: this.$refs.gstc,
        state,
      })
      this.gstc.api.scrollToTime(new Date(this.today).getTime())
      this.$watch(
        'config',
        config => {
          state.update('config', current => {
            if (current !== config) {
              return GSTC.api.mergeDeep({}, current, config)
            } else {
              return current
            }
          })
        },
        {
          deep: true,
        },
      )
    },
    destroyed () {
      this.gstc.app.destroy()
    },
    methods: {
      ...mapMutations({
        setBookingOpen: 'SET_BOOKING_OPEN',
        changeCurrentDate: 'CHANGE_CURRENT_DATE',
        setRefs: 'SET_REFS',
      }),
      ...mapActions([
        'scrollTo',
      ]),
      changePeriod () {
        GSTC.api.setPeriod(this.period)
      },
      changeZoom () {
        this.gstc.state.update('config.chart.time.zoom', +this.zoomSize)
      },
      scrollTo () {
        this.gstc.api.scrollToTime(new Date(this.today).getTime())
      },
      scrollToNew () {
        this.dialog = false
        const day = new Date(this.today)
        state.update('config.chart.time', time => {
          time.leftGlobal = window.GSTCInstance.api.time
            .date(day)
            .endOf('day')
            .valueOf()
          time.from = window.GSTCInstance.api.time
            .date(day)
            .startOf('day')
            .valueOf()
          time.to = window.GSTCInstance.api.time
            .date(day.setDate(day.getDate() + this.period))
            .endOf('day')
            .valueOf()
          if (this.today === 90) {
            time.zoom = 19
          }
          return time
        })
      },
      previousMonth () {
        state.update('config.chart.time', time => {
          time.from = window.GSTCInstance.api.time
            .date(time.from)
            .subtract(1, 'day')
            .startOf('day')
            .valueOf()
          time.to = window.GSTCInstance.api.time
            .date(time.to)
            .subtract(1, 'day')
            .endOf('day')
            .valueOf()
          return time
        })
      },
      nextMonth () {
        state.update('config.chart.time', time => {
          time.from = window.GSTCInstance.api.time
            .date(time.from)
            .add(1, 'day')
            .startOf('day')
            .valueOf()
          time.to = window.GSTCInstance.api.time
            .date(time.to)
            .add(1, 'day')
            .endOf('day')
            .valueOf()
          return time
        })
      },

      find () {
      },
    },
  }
</script>
