<template>
  <v-container>
    <v-row>
      <v-col>
        Новое бронирование
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
      >
        <v-menu
          ref="indatemenu"
          v-model="menu"
          :close-on-content-click="false"
          :return-value.sync="$store.state.start"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="$store.state.start"
              label="Заезд*:"
              readonly
              v-bind="attrs"
              prepend-icon="mdi-calendar-blank-multiple"
              v-on="on"
            />
          </template>
          <v-date-picker
            v-model="$store.state.start"
            no-title
            scrollable
          >
            <v-spacer />
            <v-btn
              text
              color="primary"
              @click="menu = false"
            >
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="$refs.indatemenu.save($store.state.start)"
            >
              OK
            </v-btn>
          </v-date-picker>
        </v-menu>
      </v-col>

      <v-col
        cols="12"
        sm="6"
        md="4"
      >
        <v-menu
          ref="outDateMenu"
          v-model="menu3"
          :close-on-content-click="false"
          :return-value.sync="$store.state.end"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="$store.state.end"
              label="Выезд*:"
              readonly
              v-bind="attrs"
              prepend-icon="mdi-calendar-blank-multiple"
              v-on="on"
            />
          </template>
          <v-date-picker
            v-model="$store.state.end"
            no-title
            scrollable
          >
            <v-spacer />
            <v-btn
              text
              color="primary"
              @click="menu3 = false"
            >
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="$refs.outDateMenu.save($store.state.end)"
            >
              OK
            </v-btn>
          </v-date-picker>
        </v-menu>
      </v-col>
    </v-row>
    <v-row v-if="isReserve">
    </v-row>
    <v-row>
      <v-col>
        <v-btn
          color="primary"
          @click="$store.commit('OPEN_CLOSE_BOOK')"
        >
          Забронировать
        </v-btn>
        <v-btn
          color="primary"
          @click="$store.commit('OPEN_CLOSE_TO_BOOK')"
        >
          Резерв
        </v-btn>
        <v-btn color="primary">
          Закрыть на ремонт
        </v-btn>
        <v-btn
          color="primary"
          @click="setBookingOpen"
        >
          Отмена
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import { mapMutations } from 'vuex'
  export default {
    name: 'QuestionReserve',
    data () {
      return {
        isReserve: false,
        menu: false,
        inDate: '',
        outDate: '',
        menu3: false,
        booking: {
          inDate: new Date().toISOString().substr(0, 10),
          outDate: new Date().toISOString().substr(0, 10),
        },
      }
    },
    methods: {
      ...mapMutations({
        setBookingOpen: 'SET_BOOKING_OPEN',
        setBookingData: 'SET_BOOKING_DATA',
      }),
    },
  }
</script>

<style scoped>

</style>
